"use client";

import { useState } from "react";
import Image from "next/image";
import Link from "next/link";
import { 
  ShoppingBag, X, MessageCircle, Menu, CheckCircle 
} from "lucide-react";
import { Product, Store } from "@/types";

// Import Modular Components
import Navbar from "./Navbar";
import Hero from "./Hero";
import ValueProp from "./ValueProp";
import HowItWorks from "./HowItWorks";
import TrustCenter from "./TrustCenter";
import Marketplace from "./Marketplace";
import Footer from "./Footer";

interface LandingPageWrapperProps {
  products: Product[];
  stores: Store[];
}

type CartItem = { product: Product; store: Store; qty: number };

export default function LandingPageWrapper({ products, stores }: LandingPageWrapperProps) {
  const [cart, setCart] = useState<CartItem[]>([]);
  const [isCartOpen, setIsCartOpen] = useState(false);
  const [toast, setToast] = useState<{ show: boolean; msg: string }>({ show: false, msg: "" });

  // --- TOAST HELPER ---
  const showNotification = (msg: string) => {
    setToast({ show: true, msg });
    setTimeout(() => setToast({ show: false, msg: "" }), 3000);
  };

  // --- CART LOGIC ---
  const addToCart = (product: Product) => {
    const productStore = stores.find(s => s.id === product.store_id);
    if (!productStore) return;

    setCart(prev => {
      const existing = prev.find(item => item.product.id === product.id);
      if (existing) {
        return prev.map(item => item.product.id === product.id ? { ...item, qty: item.qty + 1 } : item);
      }
      return [...prev, { product, store: productStore, qty: 1 }];
    });
    
    // UX FIX: Don't open cart, just show notification
    showNotification(`Added ${product.name} to bag`);
  };

  const removeFromCart = (productId: string) => {
    setCart(prev => prev.filter(item => item.product.id !== productId));
  };

  const cartByVendor = cart.reduce((acc, item) => {
    const storeId = item.store.id;
    if (!acc[storeId]) acc[storeId] = { store: item.store, items: [] };
    acc[storeId].items.push(item);
    return acc;
  }, {} as Record<string, { store: Store, items: CartItem[] }>);

  return (
    <div className="min-h-screen bg-white font-sans selection:bg-emerald-100">
      
      <Navbar cartCount={cart.length} onOpenCart={() => setIsCartOpen(true)} />
      <Hero />
      <ValueProp />
      <HowItWorks />
      <Marketplace products={products} stores={stores} onAddToCart={addToCart} />
      <TrustCenter />
      <Footer />

      {/* --- TOAST NOTIFICATION --- */}
      {toast.show && (
        <div className="fixed top-24 right-4 z-[60] bg-gray-900 text-white px-4 py-3 rounded-xl shadow-xl flex items-center gap-3 animate-in slide-in-from-right-10 fade-in">
           <CheckCircle size={20} className="text-green-400" />
           <span className="font-bold text-sm">{toast.msg}</span>
        </div>
      )}

      {/* --- FLOATING CART BUTTON --- */}
      {cart.length > 0 && !isCartOpen && (
        <button onClick={() => setIsCartOpen(true)} className="fixed bottom-6 right-6 bg-gray-900 text-white p-4 rounded-full shadow-2xl z-40 hover:scale-105 transition animate-in zoom-in">
           <ShoppingBag size={24} />
           <span className="absolute -top-1 -right-1 bg-red-500 w-6 h-6 rounded-full flex items-center justify-center text-xs font-bold border-2 border-white">{cart.length}</span>
        </button>
      )}

      {/* --- CART SIDEBAR (UX FIX: Width adjustments) --- */}
      {isCartOpen && (
        <div className="fixed inset-0 z-50 bg-black/60 backdrop-blur-sm flex justify-end animate-in fade-in">
          {/* Close Area (Click outside) */}
          <div className="absolute inset-0" onClick={() => setIsCartOpen(false)}></div>

          <div className="relative w-[85vw] md:w-full md:max-w-md bg-white h-full shadow-2xl p-6 overflow-y-auto animate-in slide-in-from-right duration-300">
            <div className="flex justify-between items-center mb-6">
              <h2 className="text-xl font-bold flex items-center gap-2"><ShoppingBag /> Your Cart</h2>
              <button onClick={() => setIsCartOpen(false)} className="p-2 bg-gray-100 rounded-full hover:bg-gray-200"><X size={20}/></button>
            </div>

            {Object.keys(cartByVendor).length === 0 ? (
               <div className="text-center py-10">
                 <ShoppingBag className="w-12 h-12 text-gray-200 mx-auto mb-3" />
                 <p className="text-gray-500">Your cart is empty.</p>
                 <button onClick={() => setIsCartOpen(false)} className="mt-4 text-emerald-600 font-bold text-sm">Continue Shopping</button>
               </div>
            ) : (
               Object.values(cartByVendor).map(({ store, items }) => {
                 const storeTotal = items.reduce((sum, item) => sum + (item.product.price * item.qty), 0);
                 const vendorNumber = store.whatsapp_number.replace(/\D/g, '').replace(/^0/, '234');
                 const msg = `*New Order via StoreLink Marketplace* ðŸ›ï¸\n\n${items.map(i => `- ${i.qty}x ${i.product.name}`).join('\n')}\n\n*Total: â‚¦${storeTotal.toLocaleString()}*`;
                 const whatsappLink = `https://wa.me/${vendorNumber}?text=${encodeURIComponent(msg)}`;

                 return (
                   <div key={store.id} className="mb-6 border border-gray-100 rounded-2xl p-4 bg-gray-50">
                     <h3 className="font-bold text-gray-900 border-b border-gray-200 pb-2 mb-3 flex justify-between">
                       {store.name}
                       <span className="text-emerald-600">â‚¦{storeTotal.toLocaleString()}</span>
                     </h3>
                     
                     {items.map(item => (
                       <div key={item.product.id} className="flex justify-between text-sm mb-2">
                         <span className="text-gray-600">{item.qty}x {item.product.name}</span>
                         <button onClick={() => removeFromCart(item.product.id)} className="text-red-400 hover:bg-red-50 p-1 rounded"><X size={14}/></button>
                       </div>
                     ))}

                     <a href={whatsappLink} target="_blank" className="mt-4 flex items-center justify-center gap-2 w-full bg-green-600 text-white py-3 rounded-xl font-bold text-sm hover:bg-green-700 transition">
                       <MessageCircle size={18} /> Checkout with {store.name}
                     </a>
                   </div>
                 );
               })
            )}
          </div>
        </div>
      )}

    </div>
  );
}