import Link from "next/link";
import { ArrowRight, ShoppingBag, Zap } from "lucide-react";

export default function Hero() {
  return (
    <header className="relative pt-12 pb-12 px-4 md:pt-24 md:pb-28 text-center overflow-hidden bg-white">      
      {/* Background Texture (The "Luxury" Detail) */}
      <div className="absolute inset-0 z-0 opacity-[0.03]" style={{ backgroundImage: 'radial-gradient(#000 1px, transparent 1px)', backgroundSize: '24px 24px' }}></div>
      <div className="absolute top-0 left-1/2 -translate-x-1/2 w-[800px] h-[400px] bg-emerald-50/50 rounded-full blur-[100px] -z-10"></div>

      <div className="max-w-4xl mx-auto relative z-10">
        
        {/* The Badge */}
        <div className="inline-flex items-center gap-2 px-3 py-1.5 bg-gray-900 text-white rounded-full font-bold text-[10px] md:text-xs uppercase tracking-wider mb-6 shadow-xl border border-gray-700">
           <span className="relative flex h-2 w-2">
              <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-green-400 opacity-75"></span>
              <span className="relative inline-flex rounded-full h-2 w-2 bg-green-500"></span>
           </span>
           Nigeria's #1 Social Commerce Engine
        </div>
        
        {/* The Headline (Tighter on mobile) */}
        <h1 className="text-4xl md:text-7xl font-extrabold text-gray-900 tracking-tight mb-6 leading-[1.1]">
          Your business deserves <br className="hidden md:block" />
          <span className="text-transparent bg-clip-text bg-gradient-to-r from-emerald-600 to-teal-500">a proper website.</span>
        </h1>
        
        {/* The Subhead */}
        <p className="text-base md:text-xl text-gray-500 mb-8 max-w-lg mx-auto leading-relaxed font-medium">
          Stop sending blurry photos on WhatsApp. Create a stunning store link, look professional, and get paid instantly.
        </p>
        
        {/* The Actions */}
        <div className="flex flex-col sm:flex-row justify-center gap-3 w-full sm:w-auto px-4 sm:px-0">
          <Link href="/onboarding" className="w-full sm:w-auto px-8 py-4 bg-gray-900 text-white text-base font-bold rounded-xl shadow-2xl hover:bg-gray-800 active:scale-95 transition flex items-center justify-center gap-2">
             Start Selling <ArrowRight size={18} />
          </Link>
          <a href="#marketplace" className="w-full sm:w-auto px-8 py-4 bg-white text-gray-900 border border-gray-200 text-base font-bold rounded-xl hover:bg-gray-50 active:scale-95 transition flex items-center justify-center gap-2">
             <ShoppingBag size={18} /> Marketplace
          </a>
        </div>

       
      </div>
    </header>
  );
}